<template>
  <div>
    <van-dropdown-menu>
      <van-dropdown-item title="综合" disabled />
      <van-dropdown-item
        title="价格"
        v-model="priceVal"
        :options="priceArr"
        @change="priceChange"
      />
      <van-dropdown-item
        title="分类"
        v-model="categoryVal"
        :options="filterCategory"
        @change="categoryChange"
      />
    </van-dropdown-menu>

    <van-empty
      v-if="goodsList.length == 0"
      image="search"
      description="你搜索的产品不存在"
    />
    <product v-else :goodsList="goodsList" />
  </div>
</template>
  
<script >
import product from "@/components/product";
export default {
  props: ["filterCategory", "goodsList"],
  data() {
    return {

      priceVal: 0,
      // 分类 下拉菜单的当前项
      categoryVal: 1005000,
      //价格下拉菜单的当前项
      priceArr: [
        { text: '价格从高到底', value: 'desc' },
        { text: '价格从低到高', value: 'asc' },
      ],

    }
  },
  components: {
    product,
  },
  methods: {
    categoryChange(value) {
      // console.log(value)
      // 子传父
      this.$emit('categoryChange', value)
    },
    //  价格下拉菜单的点击事件
    priceChange(value) {

      this.$emit('priceChange', value)
    }
  },
  mounted() {
    let _this = this
    // console.log(_this.filterCategory)
    //遍历这个数组
    this.filterCategory.map((val, index) => {
      if (val.checked) {
        _this.categoryVal = val.value;
      }
    })
  }
}

</script>

