<template>
  <div class="popup">
    <van-search
      v-model="itVal"
      show-action
      :placeholder="iptPlaceholder"
      @search="onSearch"
      @cancel="onCancel"
      @input="onInput"
    />
    <HistoryHot
      v-if="blackShow == 1"
      :historyKeywordList="historyKeywordList"
      :hotKeywordList="hotKeywordList"
      @tagClick="tagClick"
    ></HistoryHot>
    <MyList v-else-if="blackShow == 2" :arr="listArr" @cellclick="tagClick" />
    <MyProduct
      v-else
      :filterCategory="filterCategory"
      :goodsList="goodsList"
      @categoryChange="categoryChange"
      @priceChange="priceChange"
    />
  </div>
</template>

<script>
import { GetPopupData, GetSearchData, GetTimeData } from '@/request/api'
import HistoryHot from "@/components/HistoryHot";
import MyProduct from "@/components/MyProduct";
import MyList from "@/components/MyList";
export default {

  data() {
    return {
      itVal: "",
      iptPlaceholder: "",
      //1代表历史记录与热门搜索区域
      // 2代表列表区块
      // 3.代表产品区块
      blackShow: 1,
      // 历史区块的数组
      historyKeywordList: [],
      hotKeywordList: [],
      //传递给MyProduct.vue组件的分类数据
      filterCategory: [],
      //传递给MyProduct.vue的组件的商品列表数据
      goodsList: [],
      //价格排序
      order: 'desc',
      categoryId: 0,
      // id是默认的排序方式 price就是价格排序
      sort: 'id',
      // list数组
      listArr: []
    }
  },

  created() {
    GetPopupData().then(res => {
      // console.log(res)
      if (res.errno == 0) {
        let { defaultKeyword, historyKeywordList, hotKeywordList } = res.data
        this.iptPlaceholder = defaultKeyword.keyword
        this.historyKeywordList = historyKeywordList
        this.hotKeywordList = hotKeywordList
      }
      // console.log(this.hotKeywordList)
    })

  },

  methods: {
    categoryChange(val) {

      this.categoryId = val
      // 调用onSearch方法
      this.onSearch();
    },

    priceChange(val) {
      console.log(val)
      this.order = val;
      this.sort = "price"
      // 调用onSearch方法
      this.onSearch();
    },
    // 搜索
    onSearch() {
      //加个this 避免出现指向问题
      let _this = this
      GetSearchData({
        // 搜索请求
        keyword: this.itVal,
        page: 1,
        size: 20,
        order: _this.order,  //价格排序
        categoryId: _this.categoryId,
        sort: _this.sort
      }).then(res => {
        console.log(res)
        if (res.errno == 0) {
          // 跳转去产品区块
          this.blackShow = 3;
          let { filterCategory, goodsList } = res.data;
          let arr = JSON.parse(JSON.stringify(filterCategory).replace(/name/g, 'text').replace(/id/g, 'value'))
          _this.filterCategory = arr
          _this.goodsList = goodsList
        }
      })
    },
    // 实时搜索
    onInput() {
      // 显示列表区块
      this.blackShow = 2
      GetTimeData({ keyword: this.itVal }).then(res => {
        // console.log(res)
        if (res.errno == 0) {
          // console.log(res.data)
          this.listArr = res.data
        }
        // console.log(this.hotKeywordList)
      })
    },
    //取消按钮点击事件
    onCancel() {
      this.$router.go(-1)
    },
    // 历史区块的标签点击事件
    tagClick(val) {
      // console.log(val)
      this.itVal = val;
      this.onSearch();
    }
  },
  components: {
    HistoryHot,
    MyProduct,
    MyList
  }
};
</script>



<style  scoped  lang="less">
.popup {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background: #efefef;
}
</style>