<template>
  <van-list :finished="finished" finished-text="没有更多了">
    <van-cell
      v-for="item in arr"
      :key="item"
      :title="item"
      @click="cellclick(item)"
    />
  </van-list>
</template>

<script>
export default {
  props: ['arr'],
  data() {
    return {
      finished: true
    }
  },
  methods: {
    cellclick(item) {
      this.$emit('cellclick', item)
    }
  }
}
</script>

