<template>
  <div>
    <div class="history" v-if="ifShowHistory">
      <div class="top">
        <h4>历史记录</h4>
        <span><van-icon name="delete-o" @click="clearFn" /></span>
      </div>

      <div class="down">
        <van-tag
          v-for="(item, index) in historyKeywordList"
          plain
          type="default"
          :key="index"
          @click="tagClick(item)"
        >
          {{ item }}
        </van-tag>
      </div>
    </div>

    <!-- 热门搜索 -->
    <div class="history">
      <div class="top">
        <h4>热门搜索</h4>
        <!-- <span><van-icon name="delete-o" /></span> -->
      </div>

      <div class="down">
        <van-tag
          v-for="(item, index) in hotKeywordList"
          plain
          @click="tagClick(item.keyword)"
          :key="index"
          :type="item.is_hot == 1 ? 'danger' : 'default'"
          >{{ item.keyword }}</van-tag
        >
      </div>
    </div>
  </div>
</template>

<script>
import { ClearHistory } from '@/request/api'
export default {
  props: ['historyKeywordList', 'hotKeywordList'],
  data() {
    return {
      ifShowHistory: true
    }
  },
  created() {
  },
  methods: {
    tagClick(item) {
      // console.log(item)
      // 父传子
      this.$emit('tagClick', item)
    },
    clearFn() {
      ClearHistory().then(res => {
        console.log(res)
        if (res.errno == 0) {
          this.$toast.success('删除成功');
          this.ifShowHistory = false
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.history {
  padding: 0 2%;
  background-color: #fff;
  margin-bottom: 0.9rem;
  .top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 2rem;

    font-size: 1rem;
    h4 {
      font-weight: normal;
    }
  }
  .down {
    .van-tag {
      font-size: 1rem;
      margin-right: 10px;
      margin-bottom: 10px;
      padding: 0.3rpm;
    }
  }
}
</style>